import React from 'react';
import Layout from '../components/Layout';



import './styles/Biblia.css'

function Biblia(){

    function handleClick(e){
        console.log('Si');
    }

    return(
        <Layout>
            <main className="main">
                <section className="bible-section">
                    <h2 className="bible__title">Biblia</h2>
                    <div onClick={handleClick} className="bible-iframe-container">
                        <iframe onError={handleClick} className="bible__iframe" src="https://www.bible.com/es/bible/149/GEN.1.RVR1960" frameborder="0"></iframe>
                    </div>
                </section>
            </main>
        </Layout>
    )
}

export default Biblia;