import React from 'react';
import {Route, HashRouter, BrowserRouter, Switch} from 'react-router-dom';
import ScrollToTop from './ScrollToTop';
import './styles/TODOS.css'

import Home from '../pages/Home';
import Comollegar from '../pages/Comollegar';
import Instituto from '../pages/Instituto';
import PeticionesController from './PeticionesController';
import Admin from '../pages/Admin/Admin';
import NotFound from '../pages/NotFound';
import Biblia from '../pages/Biblia';

function App(){
    return(
        <HashRouter basename="/">
            <ScrollToTop />
            {/* <TODOS /> */}
            <Switch>
                <Route exact path="/" component={Home} />
                <Route exact path="/como-llegar" component={Comollegar}/>
                <Route exact path="/instituto" component={Instituto}/>
                <Route exact path="/peticiones" component={PeticionesController}/>
                <Route exact path="/admin" component={Admin}/>
                <Route exact path="/biblia" component={Biblia}/>
                <Route component={NotFound} />
            </Switch>
        </HashRouter>
    );
}

function TODOS(){
    return(
        <div className="TODOS-container">
            <ul className="TODOS-list">
            <h4>TODO LIST </h4>
                <li className="done">Mejorar los estilos del panel de administracion</li>
                <li className="done">Hacer el enlace de la biblia</li>
                <li>Terminar las demás páginas landing</li>
                <li className="done">Crear un archivo de texto donde lleve una lista de los bugs de la página</li>
                <li>Hacer que todos los botones funcionen, incluidos los de redes sociales</li>
                <li className="done">Crear link a la página principal con el logo del header</li>
                <li>Usar node mailer para la sección contáctanos y a ser posible para enviar un correo cada vez que se haga una petición</li>
                <li>Decidir qué hacer a continuación (Hacer los unit testing, continuar con el instituto bíblico o separar la página en dos una para el proyecto y una para la iglesia)</li>
            </ul>
        </div>
    )
}

export default App;